import React, { Component } from 'react';
import Signin from './components/Signin';
import Carica from './components/Carica';
import Scrollamelo from './components/Scrollamelo';
import Rimuovi from './components/Rimuovi';
import Particles from 'react-particles-js';
import CardList from './components/CardList';
import Scroll from './components/Scroll';
import Galleria from './components/Galleria';
import Frecce from './components/Frecce';
import SfondoGalleria from './components/SfondoGalleria';
import TopWrapper from './components/TopWrapper';
import '../node_modules/tachyons';


import './App.css';

class App extends Component {

componentWillMount(){



    if(
      navigator.userAgent.match(/Android/i) ||
      navigator.userAgent.match(/webOS/i) ||
      navigator.userAgent.match(/iPhone/i) ||
      navigator.userAgent.match(/iPod/i) ||
      navigator.userAgent.match(/BlackBerry/i) ||
      navigator.userAgent.match(/Opera Mini/i) ||
      navigator.userAgent.match(/IEMobile/i) ||
      navigator.userAgent.match(/Windows Phone/i)
      ){this.setState({displayFreccia:"opacity1"});
        this.setState({type:'mobile'});}
      else{
        this.setState({xshow:"displayNone"});
        this.setState({type:'desk'});
      }


    this.setState({inWidth:window.innerWidth});
    this.setState({inHeight:window.innerHeight});
    if(window.innerHeight>window.innerWidth){
      this.setState({h:'auto'});
      this.setState({w:'85%'});
      this.setState({check:0});



      }else{
        this.setState({h:300});
        this.setState({w:'auto'});
        this.setState({check:1});


    }

}

constructor(){
    super();
    this.state ={
      form:"",
      robots: [],
      robotsFil: [],
      searchfield :'show',
      currentid:undefined,
      align:'',
      check:0,
      modalita :"",
      overflow:"overflowAuto",
      accesso:"negato",
      topImg:"0",
      inWidth:0,
      inHeight:0,
      myVar:"",
      hfreccia: window.innerHeight*0.08,
      wfreccia: window.innerWidth*0.06,
      xshow:"",
      displayFreccia:"opacity0",
      access:'negato',
      type:'',
      route:'signin',
      isSignedIn:false,
      user:{       id:'',
        nome:'',
        email:'',
        entries:0,
        joined:''
      },
      parParmobile: {"particles": {
    "number": {
      "value": 35,
      "density": {
        "enable": false,
        "value_area": 800
      }
    },
    "color": {
      "value": "#ffffff"
    },
    "shape": {
      "type": "circle",
      "stroke": {
        "width": 0,
        "color": "#000000"
      },
      "polygon": {
        "nb_sides": 5
      },
      "image": {
        "src": "",
        "width": 100,
        "height": 100
      }
    },
    "opacity": {
      "value": 0.2498083922587271,
      "random": true,
      "anim": {
        "enable": false,
        "speed": 1,
        "opacity_min": 0.01,
        "sync": false
      }
    },
    "size": {
      "value": 34.5,
      "random": true,
      "anim": {
        "enable": false,
        "speed": 40,
        "size_min": 0.1,
        "sync": false
      }
    },
    "line_linked": {
      "enable": false,
      "distance": 150,
      "color": "#ffffff",
      "opacity": 0.4,
      "width": 1
    },
    "move": {
      "enable": true,
      "speed": 1.85,
      "direction": "top-right",
      "random": false,
      "straight": false,
      "out_mode": "out",
      "bounce": false,
      "attract": {
        "enable": false,
        "rotateX": 600,
        "rotateY": 1200
      }
    }},"interactivity": {
    "detect_on": "window",
    "events": {
      "onhover": {
        "enable": false,
        "mode": "repulse"
      },
      "onclick": {
        "enable": true,
        "mode": "repulse"
      },
      "resize": true
    },
    "modes": {
      "grab": {
        "distance": 400,
        "line_linked": {
          "opacity": 1
        }
      },
      "bubble": {
        "distance": 400,
        "size": 40,
        "duration": 2,
        "opacity": 8,
        "speed": 3
      },
      "repulse": {
        "distance": 50,
        "duration": 0.4
      },
      "push": {
        "particles_nb": 4
      },
      "remove": {
        "particles_nb": 2
      }
    }},"retina_detect": true},
      parPardesk: {"particles": {"number": {"value": 60,"density": {"enable": false,"value_area": 800}},"color": {"value": "#ffffff"},"shape": {"type": "circle","stroke": {"width": 0,"color": "#000000"},"polygon": {  "nb_sides": 5},"image": {"src": "","width": 100,"height": 100}},"opacity": {"value": 0.2498083922587271,"random": true,"anim": {"enable": false,"speed": 1,"opacity_min": 0.01,"sync": false}},"size": {"value": 34.5,"random": true,"anim": {  "enable": false,  "speed": 40,  "size_min": 0.1,  "sync": false}},"line_linked": {"enable": false,"distance": 150,"color": "#ffffff","opacity": 0.4,"width": 1},"move": {"enable": true,"speed": 1.85,"direction": "top-right","random": false,"straight": false,"out_mode": "out","bounce": false,"attract": {"enable": false,"rotateX": 600,"rotateY": 1200}}},"interactivity": {"detect_on": "window","events": {"onhover": {
        "enable": true,
        "mode": "repulse"},"onclick": {
        "enable": true,
        "mode": "repulse"},"resize": true},"modes": {
      "grab": {
        "distance": 400,
        "line_linked": {
          "opacity": 1
        }
      },
      "bubble": {
        "distance": 400,
        "size": 40,
        "duration": 2,
        "opacity": 8,
        "speed": 3
      },
      "repulse": {
        "distance": 100,
        "duration": 0.4
      },
      "push": {
        "particles_nb": 4
      },
      "remove": {
        "particles_nb": 2
      }}},"retina_detect": true}}
  }



  accessoUtente = (gino) => {
	    this.setState({access:'consentito'})
	    this.setState({user:{nome:gino.nome}})

	    window.rightGalleriaclick.addEventListener("mouseover", this.displayFrecciaSi);
	    window.leftGalleriaclick.addEventListener("mouseover", this.displayFrecciaSi);
	    window.divImgMenoFrecce.addEventListener("mouseover", this.displayFrecciaSi);
	    window.rightGalleriaclick.addEventListener("mouseout", this.displayFrecciaNo);
	    window.leftGalleriaclick.addEventListener("mouseout", this.displayFrecciaNo);
	    window.divImgMenoFrecce.addEventListener("mouseout", this.displayFrecciaNo);

	    fetch("https://agile-forest-45123.herokuapp.com/foto")
	      .then(resp => resp.json())
	      .then(users => this.setState({robots:users}))

	}

	fetchFoto = () =>{

		fetch("https://agile-forest-45123.herokuapp.com/foto")
	      .then(resp => resp.json())
	      .then(users => this.setState({robots:users}))
	}

  onClickFoto = (event) =>{

    this.setState({modalita:"sfondogallery"});
    this.setState({overflow:"overflowHidden"});
    window.document.body.className = "overflowHidden";
    this.setState({currentid:event.target.id});
    //Scroll
      //console.log(event.target.id)
      //console.log(this.state.robotsFil)
    //this.creaCI();
    if(window.imgCerca){
    window.imgCerca.classList.toggle("z-1")
    window.imgCarica.classList.toggle("z-1")
                        }

  }



  creaCI = () => {
    const inWidth = this.state.inWidth;
    const inHeight = this.state.inHeight;
    window.currentimg.style.width = "auto";
    window.currentimg.style.height  = `${this.state.inHeight*0.88}px`;

    if(window.currentimg.width=== this.state.inWidth){
      window.currentimg.style.width = `${this.state.inWidth*0.88}px`;
      window.currentimg.style.height  = `auto`;
    }


    window.currentimg.style.top = `${(this.state.inHeight-window.currentimg.height)/2}px`;
    window.currentimg.style.left = `${(this.state.inWidth-window.currentimg.width)/2}px`;


    window.topGalleria.style.width = `${inWidth}px`;
    window.topGalleria.style.height = `${(inHeight-window.currentimg.height)/2}px`;
    window.topGalleria.style.left = 0;

    window.leftGalleria.style.width = `${(inWidth-window.currentimg.width)/2}px`;
    window.leftGalleria.style.height = `${window.currentimg.height}px`;
    window.leftGalleria.style.top = `${(inHeight-window.currentimg.height)/2}px`;

    window.rightGalleria.style.width = `${(inWidth-window.currentimg.width)/2}px`;
    window.rightGalleria.style.height = `${window.currentimg.height}px`;
    window.rightGalleria.style.top = `${(inHeight-window.currentimg.height)/2}px`;

    window.bottomGalleria.style.width = `${inWidth}px`;
    window.bottomGalleria.style.height = `${(inHeight-window.currentimg.height)/2}px`;
    window.bottomGalleria.style.left = 0;

    window.leftGalleriaclick.style.width = `${window.currentimg.width/7.2}px`;
    window.leftGalleriaclick.style.height = `${window.currentimg.height}px`;
    window.leftGalleriaclick.style.top = `${(inHeight-window.currentimg.height)/2}px`;
    window.leftGalleriaclick.style.left = `${(inWidth-window.currentimg.width)/2}px`;

    window.divImgMenoFrecce.style.width = `${window.currentimg.width-(window.currentimg.width/3.6)}px`;
    window.divImgMenoFrecce.style.height = `${window.currentimg.height}px`;
    window.divImgMenoFrecce.style.top = `${(inHeight-window.currentimg.height)/2}px`;
    window.divImgMenoFrecce.style.left = `${((inWidth-window.currentimg.width)/2)+(window.currentimg.width/7.2)}px`;//

    window.rightGalleriaclick.style.width = `${window.currentimg.width/7.2}px`;
    window.rightGalleriaclick.style.height = `${window.currentimg.height}px`;
    window.rightGalleriaclick.style.top = `${(inHeight-window.currentimg.height)/2}px`;
    window.rightGalleriaclick.style.left = `${((inWidth-window.currentimg.width)/2)+((window.currentimg.width/7.2))+(window.currentimg.width-(window.currentimg.width/3.6))}px`;//

  }


  funzioneKBEvent = (event) => {
    if(event.code==="ArrowRight"){
      this.divImgMenoFrecceClick();
    }
    else if(event.code==="ArrowLeft"){
      this.divImgMenoFrecceClickLeft();
    }
    if(event.code==="Escape"||event.code==="Backspace"||event.code==="Delete"){
      this.outClick();
    }

    if(this.state.type==="mobile"&&this.state.access !== 'negato'&&event.which===13)
      window.barracerca.blur();

    //console.log(event)
  }


  backButtonClick = (event) => {

    if(this.state.modalita==="sfondogallery")
      this.outClick();
      else if(this.state.form==="carica")
        this.setState({form:"noncarica"})
      else
        window.history.back()

  }



  outClick = (event) => {
    this.setState({modalita:""});

    this.setState({overflow:"overflowAuto"});
    if(window.imgCerca){
    window.imgCerca.classList.toggle("z-1")
    window.imgCarica.classList.toggle("z-1")
    }
  }

  updateFinestra = (event) => {
    this.setState({modalita:""});
  }

  leftClick = (event) => {


      let index = this.state.currentid;
       index = Number(index);
      for (var i = this.state.robotsFil.length - 1; i >= 0; i--) {

        if(this.state.robotsFil[i].id===index&&i!==this.state.robotsFil.length-1){


        index = this.state.robotsFil[(i+1)].id;
        break;


        }
      }
      this.setState({currentid:(index)});
  }

  rightClick = (event) => {



      let index = this.state.currentid;
       index = Number(index);
       if(index>=0){}
      for (var i = this.state.robotsFil.length - 1; i >= 0; i--) {

        if(this.state.robotsFil[i].id===index&&i!==0){


        index = this.state.robotsFil[(i-1)].id;
        break;


        }
      }
      this.setState({currentid:(index)});





  }

  divImgMenoFrecceClick = (event) => {
    setTimeout(this.rightClick, 333);
    window.currentimg.classList.add("opacitynullI");
    window.currentimg.classList.remove("opacitytopI");
    window.frecciadx.classList.add("opacitynullI");
    window.frecciadx.classList.remove("opacitytopI");
    window.frecciasx.classList.add("opacitynullI");
    window.frecciasx.classList.remove("opacitytopI");


    setTimeout(function(){
      window.currentimg.classList.add("opacitytopI");
      window.currentimg.classList.remove("opacitynullI");
      window.frecciadx.classList.add("opacitytopI");
      window.frecciadx.classList.remove("opacitynullI");
      window.frecciasx.classList.add("opacitytopI");
      window.frecciasx.classList.remove("opacitynullI");
    }, 333);



  }

  divImgMenoFrecceClickLeft = (event) => {
    setTimeout(this.leftClick, 333);
    window.currentimg.classList.add("opacitynullI");
    window.currentimg.classList.remove("opacitytopI");
    window.frecciadx.classList.add("opacitynullI");
    window.frecciadx.classList.remove("opacitytopI");
    window.frecciasx.classList.add("opacitynullI");
    window.frecciasx.classList.remove("opacitytopI");


    setTimeout(function(){
      window.currentimg.classList.add("opacitytopI");
      window.currentimg.classList.remove("opacitynullI");
      window.frecciadx.classList.add("opacitytopI");
      window.frecciadx.classList.remove("opacitynullI");
      window.frecciasx.classList.add("opacitytopI");
      window.frecciasx.classList.remove("opacitynullI");
    }, 333);



  }



  onSearchChange = (event) =>  {
    this.setState({searchfield: event.target.value})
  }

  displayFrecciaSi = (event) =>{
    this.setState({displayFreccia:"opacity1"})
  }

  displayFrecciaNo = (event) =>{
    this.setState({displayFreccia:"opacity0"})
  }

  resizePagina = (event) =>{
    if(this.state.type==='desk')
      this.ricaricaPagina();
  }

  ricaricaPagina = (event) =>{
    window.location//.replace("https://samuelecipriani.github.io");
    .reload();

  }

  mostraFormCaricaImmagine = () =>{
    this.setState({form:"carica"})
  }

  nonCarica = () =>{

    fetch("https://agile-forest-45123.herokuapp.com/foto")
      .then(resp => resp.json())
      .then(users => this.setState({robots:users}))
      this.setState({form:"noncarica"})
  }



  componentDidMount(){


   if(this.state.type === 'mobile'){

    window.history.pushState(null, document.title, window.location.href);
    window.addEventListener('popstate', function (event){
    window.history.pushState(null, document.title, window.location.href);});
    window.addEventListener('popstate', this.backButtonClick);
    }


    window.addEventListener("keydown", this.funzioneKBEvent );




    //window.rightGalleriaclick.addEventListener("mouseover", this.displayFrecciaSi);
    //window.leftGalleriaclick.addEventListener("mouseover", this.displayFrecciaSi);
    //window.divImgMenoFrecce.addEventListener("mouseover", this.displayFrecciaSi);
    //window.rightGalleriaclick.addEventListener("mouseout", this.displayFrecciaNo);
    //window.leftGalleriaclick.addEventListener("mouseout", this.displayFrecciaNo);
    //window.divImgMenoFrecce.addEventListener("mouseout", this.displayFrecciaNo);

    //window.addEventListener("resize", this.resizePagina);
    window.addEventListener("orientationchange", this.ricaricaPagina);

  }


  render() {


    //if(this.state.robots[this.state.robots.length-1])
    //console.log(Math.abs(this.state.robots[this.state.robots.length-1].id))

    let divGallerySecondClass="";

    if(this.state.modalita==="sfondogallery"){
      divGallerySecondClass="displayInline";

    }else divGallerySecondClass="displayNone";


    const filtraRobots = this.state.robots
    // eslint-disable-next-line
    .filter((num) =>{

      if((num.id>=0)) {
        return (num.tag.toLowerCase().includes(this.state.searchfield.toLowerCase())

            );
        }else if (this.state.searchfield.toLowerCase() === 'ehyes'||this.state.searchfield.toLowerCase() === 'eh yes'){
            return num
            }

    });

    if(this.state.searchfield.toLowerCase() === 'eh yes'||this.state.searchfield.toLowerCase() === 'ehyes'){
      // eslint-disable-next-line
      this.state.accesso="yes";
      // eslint-disable-next-line
    }else{this.state.accesso="negativo";
      }

    // eslint-disable-next-line
    this.state.robotsFil=filtraRobots;


    if(this.state.form !== 'carica'){


      return (
        <div className="App">
          { this.state.type === 'mobile'
          ? <Particles className="particles" params={this.state.parParmobile} />
          : <Particles className="particles" params={this.state.parPardesk} />
          }
          {this.state.access === 'negato'
          ? <Signin accessoUtente={this.accessoUtente} onRouteChange={this.onRouteChange}/>
          : <div id="divall">

              <TopWrapper mostraFormCaricaImmagine={this.mostraFormCaricaImmagine} props={this.state} searchChange={this.onSearchChange}></TopWrapper>
              <Scrollamelo></Scrollamelo>
              <div  id="galleryMain" className="alignCenter">
                <Scroll props={this.state}>
                  <CardList click={this.onClickFoto} prop={this.state} robots={filtraRobots} />
                </Scroll>
              </div>
              <SfondoGalleria state={this.state} display={divGallerySecondClass} ></SfondoGalleria>
              <Galleria outclick={this.outClick}  load={this.creaCI} props={this.state} display={divGallerySecondClass}></Galleria>
              <Rimuovi outClick={this.outClick} fetchFoto={this.fetchFoto} nome={this.state.user.nome} mod={this.state.modalita} currentId={this.state.currentid} display={divGallerySecondClass}></Rimuovi>
              <Frecce divImgMenoFrecceClick={this.divImgMenoFrecceClick} divImgMenoFrecceClickLeft={this.divImgMenoFrecceClickLeft} leftclick={this.leftClick} rightclick={this.rightClick} display={divGallerySecondClass} props={this.state}/>
            </div>
          }


        </div>
      );
    }else{
      return (
        <div className="App">
          { this.state.type === 'mobile'
          ? <Particles className="particles" params={this.state.parParmobile} />
          : <Particles className="particles" params={this.state.parPardesk} />
          }

          <Carica fetchFoto={this.fetchFoto} lastRobotId={Math.abs(this.state.robots[this.state.robots.length-1].id)} lunghezzaRobots={this.state.robots.length} nonCarica={this.nonCarica} accessoUtente={this.accessoUtente} onRouteChange={this.onRouteChange}/>



        </div>
      );
    }





  }
}

export default App;



