import React from 'react';


const Frecce = (gino) => {







	return(
		<div className={gino.display}>

			<div
				id="divImgMenoFrecce"
				className={"posAbsolute topzero leftzero displayNone "+ gino.display}
				height={"0"}
				width={"0"}
				onClick={gino.divImgMenoFrecceClick}
				>
			</div>

			<div 	id={"leftGalleriaclick"}
					height={"0"}
					width={"0"}
					className={"posAbsolute grow "+ gino.props.displayFreccia}
					onClick={gino.divImgMenoFrecceClickLeft}
					>
						<img id="frecciasx"
						src="/foto/frecciasx.png"
						alt="frecciasx"
						height={gino.props.hfreccia}
						width={gino.props.wfreccia}
						className={"frecciaMobile "+ gino.props.displayFreccia}
						>
						</img>

			</div>




			<div 	id={"rightGalleriaclick"}
					height={"0"}
					width={"0"}
					className={"posAbsolute grow "+ gino.props.displayFreccia}
					onClick={gino.divImgMenoFrecceClick}
					>
						<img id="frecciadx"
						src="/foto/frecciadx.png"
						alt="frecciadx"
						height={gino.props.hfreccia}
						width={gino.props.wfreccia}
						className={"frecciaMobile "+ gino.props.displayFreccia}
						>
						</img>
			</div>


		</div>


	);

}


export default Frecce;



