import React from 'react';



class Rimuovi extends React.Component{

	

	onSubmitFotoRemove = () =>{
		const bool = window.confirm('sicuro di voler rimuovere questa foto?');
		if(bool){
			console.log(this.props.currentId)
			fetch('https://agile-forest-45123.herokuapp.com/fotoRemove',{
			method:'POST',
			headers:{'Content-Type':'application/json'},
			body: JSON.stringify({id:this.props.currentId})
		}).then(resp=>resp.json())
		.then(resp=>{
			this.props.fetchFoto();
			this.props.outClick();
			console.log("eliminata");
		})
		}else console.log("non eliminata")
		
		/**/
	}


	render(){
		
		let displaY="displayNone";
		let nome = this.props.nome;
		if((nome==="Samuele"||nome==="Broccola")&&this.props.mod==="sfondogallery"&&this.props.display==="displayInline")
			displaY="displayInline";


			


			
			
			return(<div  onClick={this.onSubmitFotoRemove} className={`${displaY} eliminaImmagine pl1 pr1 br2 `}><h3>Elimina Immagine</h3></div>);

			
			
	}
}


export default Rimuovi;


