import React from 'react';


const Galleria = (gino) => {

let linkMOD ="";


	

	
	//trasformo per in pixel
	if(window.Scrollamelo) {
		

		
		const gino1 = window.document.getElementById(`${gino.props.currentid}`);
		if(gino1)
		gino1.scrollIntoView()
	


	//window.Scrollamelo.scrollTo(0, pixels)}
	}




	for (var i = gino.props.robots.length - 1; i >= 0; i--) {
		if(Number(gino.props.currentid)===gino.props.robots[i].id)
		linkMOD=gino.props.robots[i].link
	}
	



	
	
	return(
		<div	className={gino.display}
					>
			<div id={"topGalleria"} height={"0"}
					width={"0"}  className="posAbsolute topzero" onClick={gino.outclick}>
				<img  	
						id={"x"} 
						className={"xchiudi displayNone" + gino.display +" "+ gino.props.xshow}
						height={"60px"}
						width={"60px"} 
						alt="x" 
						src={`/foto/x.png`}
						
				/>



			</div>
			<div id={"leftGalleria"} height={"0"}
					width={"0"}  className="posAbsolute leftzero" onClick={gino.outclick}></div>
			

			<img  	
					id={"currentimg"} 
					className={" alignCenter posAbsolute br3 displayNone" + gino.display}
					onLoad={gino.load}
					alt=""
					src={linkMOD}
					
			/>
			<div id={"rightGalleria"} height={"0"}
					width={"0"}  className="posAbsolute rightzero" onClick={gino.outclick}></div>

			<div id={"bottomGalleria"} height={"0"}
					width={"0"}  className="posAbsolute bottomzero" onClick={gino.outclick}></div>
		</div>
	
		
	);

	
	
}


export default Galleria;


