import React from 'react';


const Card = (gino) =>{
	let larghezza = 0;
	if((gino.prop.w).indexOf("%")===-1)
		larghezza = gino.prop.w
		else
			larghezza = (gino.prop.inWidth)*0.85;
	const lInk = gino.link;
	var pos = lInk.lastIndexOf("/");
	
	const lInkpart1 = lInk.slice(0, pos);
	const lInkpart2 = lInk.slice(pos, lInk.length);
	const newLink = lInkpart1 + "/ok" + lInkpart2;
 	

	return (<div className="dib">
			
			<img   

				id={`${gino.id}`}  
				className=" br3 ma2 grow bw2 shadow-5 displayNone" 
				height={gino.prop.h}  
				width={gino.prop.w} 
				alt="" 
				src={`${gino.link}`}
				onClick={gino.click}
				onLoad={
					function(){
						let immagine = document.getElementById(`${gino.id}`);
						let immagineredux = document.getElementById(`${gino.id}ok`);
						immagine.classList.toggle("displayNone");
						immagine.classList.add("dib");
						immagineredux.classList.toggle("dib");
						immagineredux.classList.add("displayNone");

						
					}
				}
			/>
			<img   
				style={{filter: 'blur(2px)'}}
				id={`${gino.id}ok`}  
				className=" dib br3 ma2 grow bw2 shadow-5" 
				height={gino.prop.h}  
				width={larghezza} 
				alt="" 
				src={newLink}
				onClick={gino.click}
				
			/>
			</div>
			
		);
}

export default Card;


