import React from 'react';

const Scroll = (props) => {
    
	if(props.props.check===0&&props.props.type!=='mobile'){
		return (
			<div id="Scrollamelo" style={	{
							overflowY: "scroll", 
							border: "5px none black",  
							height:`${props.props.inHeight-109}px`,}}>
			{props.children}
			</div>
			);
	} else if (props.props.type==='mobile'){
												if(props.props.check===0){
			
															
			
															return (
																<div id="Scrollamelo" style={	{
																				overflowY: "scroll", 
																				border: "5px none black",  
																				height:`${props.props.inHeight-69}px`,}}>
																{props.children}
																</div>
															);}else{
			
																return (
																<div id="Scrollamelo" style={	{
																				overflowY: "scroll", 
																				border: "5px none black",  
																				height:`${props.props.inHeight-69}px`,}}>
																{props.children}
																</div>
															);
															}
	} else{

		

		return (
			<div id="Scrollamelo" style={	{
							overflowY: "scroll", 
							border: "5px none black",  
							height:`${props.props.inHeight-109}px`,}}>
			{props.children}

			
			</div>
		);


	}


};

export default Scroll;


