import React from 'react';
import SearchBox from './SearchBox';


const TopWrapper = (props) => {
	
	if(props.props.check===0&&props.props.type!=='mobile'){
	return (
													<div  id="topWrapper"  className="Row ">
														<div id="imgCarica" onClick={props.mostraFormCaricaImmagine} className="z-1 absolute top-1 left-2">
															<img  src="upload.png" alt="Carica" height="69" width="69"></img>
														</div>

													    

													    <div className=" displayInline Column alignCenter ">
													    	<h1 id="titoloTop" className="z-0 opacitytop f2"> Verso l'infinito... E oltre </h1>
													    </div>

													    <div id="searchBox" className=" displayNone right-0 left-0 absolute top-2 opacitynull Column alignCenter">
													    	<SearchBox nome={props.props.user.nome} onsearchChange={props.searchChange}/>
													    </div>

													    <div id="imgCerca" onClick={function(){
													    	if(window.titoloTop.classList.contains("opacitytop")){
													    	window.searchBox.classList.toggle("displayNone")
													    	window.titoloTop.classList.toggle("opacitytop")
													    	window.titoloTop.classList.toggle("opacitynull")
														    setTimeout(function(){ 
														    	window.searchBox.classList.toggle("opacitytop")
														    	window.searchBox.classList.toggle("opacitynull")
														    	window.barracerca.focus();
													    	 }, 690);

															}else{
														    	
														    	window.searchBox.classList.toggle("opacitytop")
														    	window.searchBox.classList.toggle("opacitynull")
															    setTimeout(function(){ 
															    	window.titoloTop.classList.toggle("opacitytop")
															    	window.titoloTop.classList.toggle("opacitynull")
															    	window.barracerca.blur();
															    	window.searchBox.classList.toggle("displayNone")
														    	 }, 690);
														    	}	
														    	
													    	}} className="z-1 absolute top-2 right-2">
															<img src="search.png" alt="Cerca" height="49" width="49"></img>
														</div>

													    
													    
													    
													</div>
											);}else if(props.props.type==='mobile'){if(props.props.check===0){
																						return (
																											<div  id="topWrapper"  className="Row ">
																														<div id="imgCarica" onClick={props.mostraFormCaricaImmagine} className="z-1 absolute top14 left-1">
																															<img  src="upload.png" alt="Carica" height="41" width="41"></img>
																														</div>

																													    

																													    <div className=" displayInline Column alignCenter ">
																													    	<h2 id="titoloTop" className="z-0 opacitytop lineheight04">  Verso l'infinito... E oltre </h2>
																													    </div>

																													    <div id="searchBox" className=" displayNone right-0 left-0 absolute  opacitynull Column alignCenter">
																													    	<SearchBox nome={props.props.user.nome} onsearchChange={props.searchChange}/>
																													    </div>

																													    <div id="imgCerca"  onClick={function(){

																													    	window.barracerca.classList.remove("pa3");
																													    	window.barracerca.classList.add("padding4rem");
																													    	window.barracerca.style.width='50%';
																													    	//window.barracerca.style.top=(String
																													    	
																													    	//);

																													    	window.searchBox.style.top='19px';
																													    	
																													    	if(window.titoloTop.classList.contains("opacitytop")){
																													    	window.searchBox.classList.toggle("displayNone")
																													    	window.titoloTop.classList.toggle("opacitytop")
																													    	window.titoloTop.classList.toggle("opacitynull")
																														    setTimeout(function(){ 
																														    	window.searchBox.classList.toggle("opacitytop")
																														    	window.searchBox.classList.toggle("opacitynull")
																														    	window.barracerca.focus();
																													    	 }, 690);

																															}else{
																														    	
																														    	window.searchBox.classList.toggle("opacitytop")
																														    	window.searchBox.classList.toggle("opacitynull")
																															    setTimeout(function(){ 
																															    	window.titoloTop.classList.toggle("opacitytop")
																															    	window.titoloTop.classList.toggle("opacitynull")
																															    	window.barracerca.blur();
																															    	window.searchBox.classList.toggle("displayNone")
																														    	 }, 690);
																														    	}	
																														    	
																													    	}} className="z-1 top21 absolute right-1">
																														
																															<img src="search.png" alt="Cerca" height="27" width="27"></img>
																														</div>

																													    
																													    
																													    
																													</div>
																										);}else{ 

																							return (
																											<div  id="topWrapper"  className="Row ">
																														<div id="imgCarica" onClick={props.mostraFormCaricaImmagine} className="z-1 absolute top14 left-2">
																															<img  src="upload.png" alt="Carica" height="41" width="41"></img>
																														</div>

																													    

																													    <div className=" displayInline Column alignCenter ">
																													    	<h2 id="titoloTop" className="z-0 opacitytop lineheight04">  Verso l'infinito... E oltre </h2>
																													    </div>

																													    <div id="searchBox" className=" displayNone right-0 left-0 absolute  opacitynull Column alignCenter">
																													    	<SearchBox nome={props.props.user.nome} onsearchChange={props.searchChange}/>
																													    </div>

																													    <div id="imgCerca"  onClick={function(){

																													    	window.barracerca.classList.remove("pa3");
																													    	window.barracerca.classList.add("padding4rem");
																													    	window.barracerca.style.width='50%';
																													    	//window.barracerca.style.top=(String
																													    	
																													    	//);

																													    	window.searchBox.style.top='19px';
																													    	
																													    	if(window.titoloTop.classList.contains("opacitytop")){
																													    	window.searchBox.classList.toggle("displayNone")
																													    	window.titoloTop.classList.toggle("opacitytop")
																													    	window.titoloTop.classList.toggle("opacitynull")
																														    setTimeout(function(){ 
																														    	window.searchBox.classList.toggle("opacitytop")
																														    	window.searchBox.classList.toggle("opacitynull")
																														    	window.barracerca.focus();
																													    	 }, 690);

																															}else{
																														    	
																														    	window.searchBox.classList.toggle("opacitytop")
																														    	window.searchBox.classList.toggle("opacitynull")
																															    setTimeout(function(){ 
																															    	window.titoloTop.classList.toggle("opacitytop")
																															    	window.titoloTop.classList.toggle("opacitynull")
																															    	window.barracerca.blur();
																															    	window.searchBox.classList.toggle("displayNone")
																														    	 }, 690);
																														    	}	
																														    	
																													    	}} className="z-1 top21 absolute right-2">
																														
																															<img src="search.png" alt="Cerca" height="27" width="27"></img>
																														</div>

																													    
																													    
																													    
																													</div>
																										);

																						}




																					} else { 
														if(window.innerWidth>= 1057){
								return (	
													<div  id="topWrapper"  className="Row ">
														
													    
														<div className="Column alignCenter">
															<h1 className="f3 pointer underline-hover" onClick={props.mostraFormCaricaImmagine}> Carica Immagine </h1>
														</div>

													    

													    <div className="Column alignCenter">
													    	<h1 className="f2"> Verso l'infinito... E oltre </h1>
													    </div>

													    <div className="Column alignCenter">
													    	<SearchBox nome={props.props.user.nome} onsearchChange={props.searchChange}/>
													    </div>

													    
													    
													    
													</div>
												);}else{

									return (
													<div  id="topWrapper"  className="Row ">
														<div id="imgCarica" onClick={props.mostraFormCaricaImmagine} className="z-1 absolute top-1 left-2">
															<img  src="upload.png" alt="Carica" height="69" width="69"></img>
														</div>

													    

													    <div className=" displayInline Column alignCenter ">
													    	<h1 id="titoloTop" className="z-0 opacitytop f2"> Verso l'infinito... E oltre </h1>
													    </div>

													    <div id="searchBox" className=" displayNone right-0 left-0 absolute top-2 opacitynull Column alignCenter">
													    	<SearchBox nome={props.props.user.nome} onsearchChange={props.searchChange}/>
													    </div>

													    <div id="imgCerca" onClick={function(){
													    	if(window.titoloTop.classList.contains("opacitytop")){
													    	window.searchBox.classList.toggle("displayNone")
													    	window.titoloTop.classList.toggle("opacitytop")
													    	window.titoloTop.classList.toggle("opacitynull")
														    setTimeout(function(){ 
														    	window.searchBox.classList.toggle("opacitytop")
														    	window.searchBox.classList.toggle("opacitynull")
														    	window.barracerca.focus();
													    	 }, 690);

															}else{
														    	
														    	window.searchBox.classList.toggle("opacitytop")
														    	window.searchBox.classList.toggle("opacitynull")
															    setTimeout(function(){ 
															    	window.titoloTop.classList.toggle("opacitytop")
															    	window.titoloTop.classList.toggle("opacitynull")
															    	window.barracerca.blur();
															    	window.searchBox.classList.toggle("displayNone")
														    	 }, 690);
														    	}	
														    	
													    	}} className="z-1 absolute top-2 right-2">
															<img src="search.png" alt="Cerca" height="49" width="49"></img>
														</div>

													    
													    
													    
													</div>
												);

								}
						}
};

export default TopWrapper;


